import React from 'react';
import {appSettingModel} from '../foundation';
import {connect} from "react-redux";
import {updateAppSettings} from "../../actions/foundationActions";
import {updateTesting} from "../../actions/testingActions";

const FoundationTest = (props) => {
    return (<div id="foundationTest">
        <div>
            <button id="getDataModel" onClick={()=>{props.updateTesting({dataModel:appSettingModel()})}}>GetFoundation Data model</button>
        </div>
        Foundation Tests</div>)
};

const mapStateToProps = (state) =>{
    return {
        appSettings:state.appSettings,
        dataStore:state.dataStore,
        user:state.user,
        gameTime:state.gameTime
    };
};

export default connect(mapStateToProps,{
    updateAppSettings,
    updateTesting
})(FoundationTest);
